import React, { useState, useEffect, useRef } from 'react';
import { Button } from '../Interface/Button';
import { TextEditor } from './TextEditor';
import { Settings, MediaImage } from 'iconoir-react';
import { wordPressService } from '../../services/wordpress';
import styles from './Editor.module.css';

const { ipcRenderer } = window.require('electron');
const Store = window.require('electron-store');
const store = new Store();

export function Editor({ onError, onSuccess, onCredentialsReset }) {
  const [content, setContent] = useState('');
  const [title, setTitle] = useState('');
  const [isPublishing, setIsPublishing] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  const [showTitleField, setShowTitleField] = useState(store.get('showTitleField', true));
  const titleRef = useRef(null);
  const fileInputRef = useRef(null);

  useEffect(() => {
    // Watch for changes to the showTitleField setting
    const handleSettingsChange = () => {
      setShowTitleField(store.get('showTitleField', true));
    };

    // Set up an interval to check for settings changes
    const interval = setInterval(handleSettingsChange, 1000);

    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    // Keep the title in sync with state
    if (titleRef.current && titleRef.current.textContent !== title) {
      titleRef.current.textContent = title;
    }
  }, [title]);

  const handleContentChange = (e) => {
    setContent(e.target.value);
  };

  const handleTitleInput = (e) => {
    const newTitle = e.target.textContent;
    setTitle(newTitle);
  };

  const handlePaste = (e) => {
    e.preventDefault();
    const text = e.clipboardData.getData('text/plain');
    document.execCommand('insertText', false, text);
  };

  const handleImageClick = () => {
    if (selectedImage) {
      // Remove the image
      setSelectedImage(null);
      setImagePreview(null);
      // Reset the file input
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    } else {
      fileInputRef.current?.click();
    }
  };

  const handleImageSelect = (e) => {
    const file = e.target.files?.[0];
    if (file) {
      setSelectedImage(file);
      // Create preview URL
      const previewUrl = URL.createObjectURL(file);
      setImagePreview(previewUrl);
    }
  };

  // Cleanup preview URL when component unmounts or image changes
  useEffect(() => {
    return () => {
      if (imagePreview) {
        URL.revokeObjectURL(imagePreview);
      }
    };
  }, [imagePreview]);

  const handleSettingsClick = async () => {
    try {
      await ipcRenderer.invoke('open-settings');
    } catch (error) {
      console.error('Error opening settings:', error);
      onError('Failed to open settings');
    }
  };

  const handlePublish = async () => {
    try {
      if (!content.trim()) {
        onError('Please write something before publishing.');
        return;
      }

      setIsPublishing(true);

      let uploadedMedia = null;
      if (selectedImage) {
        uploadedMedia = await wordPressService.uploadMedia(selectedImage);
      }

      await wordPressService.createPost(content, uploadedMedia, title);
      
      setContent('');
      setTitle('');
      setSelectedImage(null);
      onSuccess('Post published successfully!');
    } catch (error) {
      console.error('Publishing error:', error);
      onError('Error publishing post: ' + error.message);
    } finally {
      setIsPublishing(false);
    }
  };

  return (
    <div className={styles.container}>
      {showTitleField && (
        <div
          ref={titleRef}
          className={styles.title}
          contentEditable
          onInput={handleTitleInput}
          onPaste={handlePaste}
          role="textbox"
          aria-label="Post title"
          data-placeholder="add title"
        />
      )}

      <TextEditor 
        content={content}
        onChange={handleContentChange}
      />

      <div className={styles.actions}>
        <div className={styles.leftActions}>
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleImageSelect}
            accept="image/jpeg,image/png,image/gif,image/webp"
            style={{ display: 'none' }}
          />
          {imagePreview ? (
            <div className={styles.imagePreview} onClick={handleImageClick} role="button" aria-label="Remove image">
              <img src={imagePreview} alt="Selected" />
            </div>
          ) : (
            <Button 
              variant="default" 
              onClick={handleImageClick}
              icon={MediaImage}
            >
              Select image
            </Button>
          )}
        </div>

        <div className={styles.rightActions}>
          <Button
            variant="default"
            icon={Settings}
            onClick={handleSettingsClick}
            className={styles.settingsButton}
          >
            Settings
          </Button>
          <Button 
            variant="primary"
            onClick={handlePublish}
            disabled={isPublishing || !content.trim()}
            className={styles.publishButton}
          >
            {isPublishing ? 'Publishing...' : 'Publish'}
          </Button>
        </div>
      </div>
    </div>
  );
} 